<template>
  <div class="loginarea">
    <div class="login_text">
        <span>로그인22</span>
    </div>
    <div class="logininput">
      <input type="text" placeholder="제품명" id="pname" v-model="pname"><br>
      <input type="password" placeholder="제품가격" id="pprice" v-model="pprice">
      <input type="password" placeholder="제품수량" id="pcount" v-model="pcount">
   </div>
   <div class="loginbtn">
      <button type="button" class="btn btn-primary" @click="addproduct()">제품등록</button>
      <button type="button" class="btn btn-secondary">뒤로가기</button>
   </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'MainPage',
  data(){
    return{
        pname:'',
        pprice: '',
        pcount: ''
    }
  },
  methods:{
    addproduct(){

        var url = 'http://localhost:8080/addproduct';
        var data = {
          pname : this.pname,
          pprice : this.pprice,
          pcount : this.pcount
        }
        axios.get(url,(data)
    )
    .then(response => {
      console.log(response)
    })
    .catch(error => {
        console.log(error);
    })
  }
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .login_text{
        height: 150px;
        display: block;
        margin: 0 auto;
        margin-top: 50px;
        text-align: center;
    }
    .login_text span{
        font-size: 30px;
        font-weight: bold;
        color: #002b36;
    }
    .logininput input{
        width: 245px;
        margin-bottom: 5px;
    }
    .loginbtn button{
        width: 120px;
        margin-right: 5px;
        margin-bottom: 262px;
    }
</style>
